@use "../../styles/vars.scss";

.sidebar {
  width: 40px;
  position: fixed;
  bottom: 0;
  color: vars.$slate;
  z-index: 10;
  @media (max-width: 768px) {
    display: none;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  &::after {
    content: "";
    background-color: vars.$lightest-slate;
    display: block;
    width: 1px;
    height: 90px;
  }
}

.wrapper a {
  padding: 10px;
  transition: vars.$transition;
  &:hover {
    color: vars.$american-silver;
  }
}

.left {
  left: 40px;
  right: auto;
  .list {
    list-style: none;
    margin: 0;
    padding: 0;
    li a {
      font-size: vars.$fs-xl;
      padding: 12px;
    }
  }
}

.right {
  right: 40px;
  left: auto;
  a {
    display: inline-block;
    margin: 20px auto;
    writing-mode: vertical-rl;
    letter-spacing: 0.1em;
    font-size: vars.$fs-xxs;
    line-height: vars.$fs-lg;
    font-family: vars.$font-mono;
  }
}
